import React, { FC, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { RouteComponentProps, withRouter } from 'react-router';
import { Button, Col, Form, FormFeedback, FormGroup, Input, Row, UncontrolledTooltip } from 'reactstrap';
import {
  environment,
  ERROR,
  INFORMATION,
  loginActions,
  loginToken,
  recuperarLogin,
  showToast,
  useLocalStorage,
  validarLoginController,
  WARNING,
} from 'summer';
import logo from '../../../assets/logo.png';
import { existeEmpresaCadastroInvalido, obterGlobalParameter } from '../../../services/ger.service';
import { setGlobalParameter } from '../../../store/ducks/global.duck';

interface LoginForm {
  usuario: string;
  senha: string;
  codigoCliente?: string;
}

const initialState: LoginForm = {
  senha: '',
  usuario: '',
};

enum ViewStates {
  LOGIN,
  RECUPERAR,
  TOKEN,
}

const REQUIRED_FIELD = 'Campo obrigatório';

const LoginForm: FC<RouteComponentProps> = ({ history, location }) => {
  const [codigoClienteStorage, writeCodigoClienteStorage] = useLocalStorage('codigoCliente');
  const [loginTokenStorage, writeLoginTokenStorage] = useLocalStorage('loginToken');
  const [currentState, setCurrentState] = useState(ViewStates.LOGIN);

  initialState.codigoCliente = codigoClienteStorage;
  const { register, errors, handleSubmit, triggerValidation } = useForm<LoginForm>({ defaultValues: initialState, mode: 'onBlur' });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const dispatch = useDispatch();

  const onSubmit = async (data: LoginForm, event: any) => {
    event.preventDefault();
    setIsSubmitting(true);

    const errorFunc = mensagem => {
      showToast(mensagem, ERROR);
      setIsSubmitting(false);
    };
    const efetuarLogin = () => {
      validarLoginController({
        codigoCliente: data.codigoCliente,
        errorFunc,
        login: data.usuario,
        resultFunc: (result, tokenAcesso) => {
          writeLoginTokenStorage(tokenAcesso);
          if (environment.production) {
            environment.siteURL = result.urlSistema;
          }
          writeCodigoClienteStorage(data.codigoCliente);
          obterGlobalParameter(
            {
              codigoCliente: data.codigoCliente,
              login: data.usuario,
              senha: data.senha,
            },
            {
              errorFunction: errorFunc,
              thenFunction: globalParameter => {
                dispatch(
                  loginActions.login({
                    codigoCliente: data.codigoCliente,
                    usuario: data.usuario,
                  })
                );

                if (globalParameter.usuario.administrador || globalParameter.usuario.id === 1) {
                  existeEmpresaCadastroInvalido(globalParameter.usuario, {
                    errorFunction: errorFunc,
                    thenFunction: companyIsValid => {
                      if (companyIsValid) {
                        dispatch(setGlobalParameter(globalParameter));
                        history.push(location.pathname + 'ger/empresaSearchView');
                      } else {
                        dispatch(setGlobalParameter(globalParameter));
                        history.push(location.pathname + 'ger/dashboard');
                      }
                    },
                  });
                } else {
                  dispatch(setGlobalParameter(globalParameter));
                  history.push(location.pathname + 'ger/dashboard');
                }
              },
            }
          );
        },
        senha: data.senha,
        tokenAcesso: loginTokenStorage,
        warningFunc: (mensagem, urlAcesso) => {
          let toastComp: any = null;
          if (urlAcesso) {
            const onClickHandler = () => window.open(urlAcesso, '_blank');
            toastComp = <div onClick={onClickHandler}>${mensagem}</div>;
          } else {
            toastComp = mensagem;
          }
          showToast(toastComp, WARNING);
          setIsSubmitting(false);
        },
      });
    };
    switch (currentState) {
      case ViewStates.RECUPERAR:
        recuperarLogin({
          errorFunc,
          login: data.usuario,
          resultFunc: (result, tokenAcesso) => {
            writeLoginTokenStorage(tokenAcesso);
            setCurrentState(ViewStates.LOGIN);
            showToast('Sua nova senha foi enviada para o e-mail informado. Por favor verifique sua caixa de entrada.', INFORMATION);
          },
          tokenAcesso: loginTokenStorage,
        });
        break;
      case ViewStates.TOKEN:
        loginToken({
          errorFunc,
          resultFunc: (result, tokenAcesso) => {
            writeLoginTokenStorage(tokenAcesso);
            data.senha = result.senha;
            data.usuario = result.usuario;
            efetuarLogin();
          },
          token: data.senha,
          tokenAcesso: loginTokenStorage,
        });
        break;
      default:
        efetuarLogin();
    }
  };

  const tokenClickHandler = () => {
    setCurrentState(ViewStates.TOKEN);
    triggerValidation();
  };
  const recuperarClickHandler = () => {
    setCurrentState(ViewStates.RECUPERAR);
    triggerValidation();
  };
  const voltarLogin = () => {
    setCurrentState(ViewStates.LOGIN);
    triggerValidation();
  };

  const invalidCode = !!(errors.codigoCliente && errors.codigoCliente.message);
  const invalidUser = !!(errors.usuario && errors.usuario.message);
  const invalidPass = !!(errors.senha && errors.senha.message);

  const backButton = (
    <Button size="sm" color="link" block={true} onClick={voltarLogin}>
      Voltar
    </Button>
  );

  const loginForm =
    currentState === ViewStates.LOGIN ? (
      <>
        <FormGroup>
          <label>Código</label>
          <Input
            placeholder="Informe seu código"
            type="number"
            id="codigoCliente"
            name="codigoCliente"
            innerRef={register({ required: REQUIRED_FIELD })}
            invalid={invalidCode}
          />
          <FormFeedback>{REQUIRED_FIELD}</FormFeedback>
          <UncontrolledTooltip target="codigoCliente" placement="bottom">
            Esse código corresponde ao Código do Cliente no Cadastro da Projetus. Caso tenha esquecido clique em 'Recuperar senha' para obtê-lo.
          </UncontrolledTooltip>
        </FormGroup>

        <FormGroup>
          <label>Usuário</label>
          <Input
            placeholder="Informe seu usuário"
            type="text"
            name="usuario"
            innerRef={register({ required: REQUIRED_FIELD })}
            invalid={invalidUser}
          />
          <FormFeedback>{REQUIRED_FIELD}</FormFeedback>
        </FormGroup>

        <FormGroup>
          <label>Senha</label>
          <Input
            placeholder="Informe sua senha"
            type="password"
            name="senha"
            innerRef={register({ required: REQUIRED_FIELD })}
            invalid={invalidPass}
          />
          <FormFeedback>{REQUIRED_FIELD}</FormFeedback>
        </FormGroup>

        <Button size="lg" block={true} color="first" disabled={isSubmitting} className="mb-2">
          Entrar
        </Button>
        <a href="https://ajuda.projetusti.com.br/index.php" target="_blank" rel="noopener noreferrer" className="btn btn-neutral-dark btn-block">
          Ajuda
        </a>
        <div className="d-flex justify-content-between pt-3">
          <Button size="sm" color="link" className="px-0" onClick={tokenClickHandler}>
            Login via Token
          </Button>
          <Button size="sm" color="link" className="px-0" onClick={recuperarClickHandler}>
            Recuperar senha
          </Button>
        </div>
      </>
    ) : null;

  const tokenForm =
    currentState === ViewStates.TOKEN ? (
      <>
        <FormGroup>
          <label>Código</label>
          <Input
            placeholder="Informe seu código"
            type="number"
            id="codigoCliente"
            name="codigoCliente"
            innerRef={register({ required: REQUIRED_FIELD })}
            invalid={invalidCode}
          />
          <FormFeedback>{REQUIRED_FIELD}</FormFeedback>
          <UncontrolledTooltip target="codigoCliente" placement="bottom">
            Esse código corresponde ao Código do Cliente do Cadastro da Projetus. Caso tenha esquecido clique em 'Recuperar senha' para obtê-lo.
          </UncontrolledTooltip>
        </FormGroup>

        <FormGroup>
          <label>Token</label>
          <Input placeholder="Informe o token" type="password" name="senha" innerRef={register({ required: REQUIRED_FIELD })} invalid={invalidPass} />
          <FormFeedback>{REQUIRED_FIELD}</FormFeedback>
        </FormGroup>

        <Button size="lg" block={true} color="first" disabled={isSubmitting}>
          Entrar
        </Button>
        {backButton}
      </>
    ) : null;

  const recuperarForm =
    currentState === ViewStates.RECUPERAR ? (
      <>
        <FormGroup>
          <label>Usuário</label>
          <Input
            placeholder="Informe seu usuário ou e-mail"
            type="text"
            name="usuario"
            innerRef={register({ required: REQUIRED_FIELD })}
            invalid={invalidUser}
          />
          <FormFeedback>{REQUIRED_FIELD}</FormFeedback>
        </FormGroup>

        <Button size="lg" block={true} color="first" disabled={isSubmitting}>
          Recuperar
        </Button>
        {backButton}
      </>
    ) : null;

  return (
    <div className="app-wrapper min-vh-100">
      <div className="app-main min-vh-100">
        <div className="app-content p-0">
          <div className="app-content--inner d-flex align-items-center w-100">
            <div className="container">
              <Row className="justify-content-center">
                <Col xl={5} lg={6} md={8} xs={12} className="w-100 d-flex flex-column">
                  <div className="text-center">
                    <img src={logo} alt="Calima ERP" />
                  </div>
                  <div className="w-100 bg-white rounded p-4 my-4">
                    <Form noValidate={true} onSubmit={handleSubmit(onSubmit)}>
                      {loginForm}
                      {tokenForm}
                      {recuperarForm}
                    </Form>
                  </div>
                  <div className="text-center pt-4 text-black-50">
                    Não possui login?{' '}
                    <strong>
                      <a href="https://centraldocliente.projetusti.com.br/#/cadastro" target="_blank" rel="noopener noreferrer">
                        Cadastre-se agora!
                      </a>
                    </strong>
                  </div>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
const LoginRouter = withRouter(LoginForm);

export default LoginRouter;
